import React from 'react';
import { ToolbarSection, ToolbarItem } from '../../editor';
import { ToolbarRadio } from '../../editor/Toolbar/ToolbarRadio';
import style  from '../Selectors.module.css';

export const ButtonSettings = () => {
  return (
    <React.Fragment>
      <ToolbarSection
        title="URL"
        props={[`url`]}
        summary={({ url }) => {
          return (
            <p>{url}</p>
          );
        }}
      >
        <ToolbarItem
          full={true}
          propKey="url"
          type="url"
          label="URL"
        />
      </ToolbarSection>
      <ToolbarSection
        title="Colors"
        props={[`background`, `color`]}
        summary={({ background, color }) => {
          return (
            <div>
              <div style={{background: background && `rgba(${Object.values(background)})`}}
                className={style.textPlaceholder}>
                <p style={{color: color && `rgba(${Object.values(color)})`}}
                  className={style.textPlaceholderText}>
                  Text
                </p>
              </div>
            </div>
          );
        }}
      >
        <ToolbarItem
          full={true}
          propKey="background"
          type="bg"
          label="Background"
        />
        <ToolbarItem full={true} propKey="color" type="color" label="Text" />
      </ToolbarSection>
      <ToolbarSection
        title="Margin"
        props={[`margin`]}
        summary={({ margin }) => {
          return `${margin[0] || 0}px ${margin[1] || 0}px ${margin[2] || 0}px ${
            margin[3] || 0
          }px`;
        }}
      >
        <ToolbarItem propKey="margin" index={0} type="slider" label="Top" />
        <ToolbarItem propKey="margin" index={1} type="slider" label="Right" />
        <ToolbarItem propKey="margin" index={2} type="slider" label="Bottom" />
        <ToolbarItem propKey="margin" index={3} type="slider" label="Left" />
      </ToolbarSection>
      <ToolbarSection title="Decoration">
        <ToolbarItem propKey="buttonStyle" type="radio" label="Style">
          <ToolbarRadio value="full" label="Full" />
          <ToolbarRadio value="outline" label="Outline" />
        </ToolbarItem>
      </ToolbarSection>
    </React.Fragment>
  );
};
